@import url('https://fonts.googleapis.com/css?family=Oswald:300,400,500,600&display=swap');

$darkpurple : #18131d;
$purple :#4f445a;
$lightpurple : #7d7883;

#options{
	text-align: left;
	width: 80%;
	margin: 0 auto;
	h1, h2, .description{
		font-family: 'Oswald', sans-serif;
		font-weight: 500;
		text-transform: uppercase;
		margin: 0;
	}
	h1{
		color: $purple;
		font-size: 35px;
		& + .description{
			font-size: 16px;
			color: white;
			font-weight: 300;
		}
	}
	h2{
		color: $lightpurple;
		font-size: 20px;
		font-weight: 500;
		margin-top: 25px;
		margin-bottom: 10px;
	}
	button.option{
		display: inline-block;
		border: 3px dashed $lightpurple;
		//background: transparent;
	
		min-height: 150px;
		margin: 0 15px 65px 0;

		position: relative;
		span.touch{
			box-sizing: border-box;
			font-family: 'Oswald', sans-serif;
			position: absolute;
			bottom: -8px;
			left: -3px;
			width: calc( 100% + 6px );
			background: $lightpurple;
			display: block;
			padding: 10px;
			color: $darkpurple;
			font-weight: 600;
			transform: translateY(100%);
			font-size: 14px;
			text-transform: capitalize;
		}
	}
	button.back{
		background: $purple;
		display: block;
		padding: 10px 50px;
		border: 0;
		position: fixed;
		top: 10px;
		right: 10px;
		color: $darkpurple;
		border: 1px solid $purple;
		font-weight: 600;
		font-size: 16px;
		text-transform: uppercase;
	}
}

$arrowBottom: url('data:image/png;base64,');
$arrowLeft: url('data:image/png;base64,');
$arrowRight: url('data:image/png;base64,');
$arrowRotateLeft: url('data:image/png;base64,');
$arrowRotateRight: url('data:image/png;base64,');



$cellWidth:30px;

button.option{ 

	width: 150px;

	&.bottom, &.left, &.right, &.rotateLeft, &.rotateRight, &.projection{
		
		background-size: 40px auto !important;
		.shape, .shape2{

			transition: background 0.3s;
			float: left;
			background: $lightpurple;
			display: block;
			position: absolute;
			&:before{
				content: " ";
				transition: background 0.3s;
				position: absolute;
				background: $lightpurple;
			}
		}
	}

	&.bottom{
		background: $arrowBottom transparent center 110px no-repeat;
		.shape{
			width: 3*$cellWidth;
			height: $cellWidth;
			top: 15%;
			left: 50%;
			transform: translateX(-50%);
			&:before{
				bottom: -$cellWidth;
				left: $cellWidth;
				height: $cellWidth;
				width: $cellWidth;
			}
		}
		&:hover{
			.shape, .shape:before{
				background: #f1e903;
			}
		}
	}

	&.left{
		width: 160px !important;
		background: $arrowLeft transparent 10px center no-repeat;
		background-size: auto 40px !important;
		.shape{
			width: 2*$cellWidth;
			height: 2*$cellWidth;
			top: 30%;
			left: 60%;
			transform: translateX(-50%);
		}
		&:hover{
			.shape, .shape:before{
				background: #e3434e;
			}
		}
	}

	&.right{
		width: 160px !important;
		background: $arrowRight transparent 125px center no-repeat;
		background-size: auto 40px !important;
		.shape{
			width: 2*$cellWidth;
			height: $cellWidth;
			top: 30%;
			left: 50%;
			transform: translateX(-50%);
			&:before{
				bottom: -$cellWidth;
				left: -$cellWidth;
				height: $cellWidth;
				width: 2*$cellWidth;
			}
		}
		&:hover{
			.shape, .shape:before{
				background: #932ff6;
			}
		}
	}

	&.rotateLeft{
		width: 160px !important;
		background: $arrowRotateLeft transparent 75px 20px no-repeat;
		background-size: auto 80px !important;
		.shape{
			width: $cellWidth;
			height: 3*$cellWidth;
			top: 20%;
			left: 20%;
			transform: translateX(-50%);
			&:before{
				bottom: 0;
				left: $cellWidth;
				height: $cellWidth;
				width: $cellWidth;
			}
		}
		&:hover{
			.shape, .shape:before{
				background: #f8519c;
			}
		}
	}

	&.rotateRight{
		width: 160px !important;
		background: $arrowRotateRight transparent center 20px no-repeat;
		background-size: 80px auto !important;
		.shape{
			width: 4*$cellWidth;
			height: $cellWidth;
			bottom: 10%;
			left: 50%;
			transform: translateX(-50%);
			&:before{
				bottom: 0;
				left: $cellWidth;
				height: $cellWidth;
				width: $cellWidth;
			}
		}
		&:hover{
			.shape, .shape:before{
				background: #6484fc;
			}
		}
	}

	&.projection{
		height: 200px;
		background: transparent;
		.shape, .shape2{
			width: 2*$cellWidth;
			height: $cellWidth;
			top: 0;
			left: 50%;
			margin-left: -3/2*$cellWidth;
			&:before{
				bottom: -$cellWidth;
				left: $cellWidth;
				height: $cellWidth;
				width: 2*$cellWidth;
			}
		}
		.shape2{
			top: unset;
			bottom: $cellWidth;
			background: #221d27;
			&:before{
				background: #221d27;
			}
		}
		&:hover{
			.shape, .shape:before{
				background: #37d3d9;
			}
		}
	}
}


